.tile {
  height: 60vh;
  flex-basis: 30%;
  margin-left: 2.5%;
  margin-top: 5%;
  border: 1px solid black;
  border-radius: 1%;
  background-color: #999999;
  cursor: pointer;
}

.tile:hover {
  opacity: 0.85;
}

.image {
  height: 75%;
  width: 100%;
  background-color: #777777;
}

.text {
  display: flex;
  align-items: center;
  text-align: left;
  height: 15%;
  width: 100;
}

.price {
  display: flex;
  align-items: center;
  height: 10%;
  width: 100%;
  font-weight: bold;
}